import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import swal from "sweetalert";
import classNames from "./PracticeBoard.module.css";
import * as reduxFunctions from "../../store/actions/index";

import Headers from "../../components/BoardHeaderRow/Row";
import Row from "../../components/PracticeBoardResultsRow/Row";

const PracticeBoard = (props) => {
  const [focusingInput, setFocusingInput] = useState({ t: {}, b: {} });
  const [carryInput, setCarryInput] = useState({});
  // const [nextCheck, setNextCheck] = useState();

  const carryFocus = (e) => {
    const carriesCell = [
      ...document.getElementsByClassName(e.target.getAttribute("class")),
    ];
    const indexOfCarry = carriesCell.indexOf(e.target);
    if (props.inputDisable) {
      if (props.carryClick) {
        carriesCell.forEach((c) => (c.style.backgroundColor = "transparent"));
        if (indexOfCarry === props.correctCarryCell) {
          // setNextCheck(indexOfCarry);
          e.target.style.backgroundColor = "#ffffc0";
          props.messagePractice(
            "You got the carry cell! Now enter the value of it."
          );
          props.pcf(true);
        } else {
          e.target.style.backgroundColor = "#ff867c";
          props.messagePractice("Sorry try again");
          props.pcf(false);
        }
      } else {
        swal(
          "Wrong Action!",
          "You have already chosen the right carry cell.",
          "error"
        );
      }
    } else {
      swal(
        "Wrong Action!",
        "Please end digits and start the mutliplication first.",
        "error"
      );
    }

    // document.getElementById("carryInputContainer").childNodes[
    //   nextCheck
    // ].style.backgroundColor = "#ffffc0";
  };

  useEffect(() => {
    setFocusingInput({ t: {}, b: {} });
    if (props.addZero) {
      setFocusingInput({ t: {}, b: {} });
    } else {
      setFocusingInput({
        t: {
          [props.t]: true,
        },
        b: {
          [props.b]: true,
        },
      });
    }
    setCarryInput({});
    setCarryInput({ [props.c]: props.carry });
  }, [props]);

  return (
    <div className={classNames.container}>
      <div className={classNames.inner}>
        <Headers />
        <div
          className={classNames.carryRow}
          onClick={(e) => {
            carryFocus(e);
          }}
          id="carryInputContainer"
        >
          <div className={classNames.carryInput}></div>
          <div className={classNames.carryInput}></div>
          <div className={classNames.carryInput}>
            <h2>{carryInput[2]}</h2>
          </div>
          <div className={classNames.carryInput}>
            <h2>{carryInput[1]}</h2>
          </div>
          <div className={classNames.carryInput}>
            <h2>{carryInput[0]}</h2>
          </div>
          <div className={classNames.carryInput}></div>
        </div>
        {/* Top Row Start */}
        <div className={classNames.numberRow}>
          <div className={classNames.numberInput}>{null}</div>
          <div className={classNames.numberInput}>{null}</div>
          <div className={classNames.numberInput}>{null}</div>
          <div
            className={classNames.numberInput}
            style={focusingInput.t[0] ? { backgroundColor: "yellow" } : null}
          >
            <h1>{props.tR[0]}</h1>
          </div>
          <div
            className={classNames.numberInput}
            style={focusingInput.t[1] ? { backgroundColor: "yellow" } : null}
          >
            <h1>{props.tR[1]}</h1>
          </div>
          <div
            className={classNames.numberInput}
            style={focusingInput.t[2] ? { backgroundColor: "yellow" } : null}
          >
            <h1>{props.tR[2]}</h1>
          </div>
        </div>
        {/* Top Row End */}
        {/* Bottom Row Start */}
        <div className={`${classNames.numberRow} ${classNames.redBorder}`}>
          <div className={classNames.numberInput}>
            <h1>X</h1>
          </div>
          <div className={classNames.numberInput}>{null}</div>
          <div className={classNames.numberInput}>{null}</div>
          <div
            className={`${classNames.numberInput} ${classNames.bR1}`}
            style={focusingInput.b[0] ? { backgroundColor: "yellow" } : null}
          >
            <h1>{props.bR[0]}</h1>
          </div>
          <div
            className={`${classNames.numberInput} ${classNames.bR2}`}
            style={focusingInput.b[1] ? { backgroundColor: "yellow" } : null}
          >
            <h1>{props.bR[1]}</h1>
          </div>
          <div
            className={`${classNames.numberInput} ${classNames.bR3}`}
            style={focusingInput.b[2] ? { backgroundColor: "yellow" } : null}
          >
            <h1>{props.bR[2]}</h1>
          </div>
        </div>
        {/* Bottom Row End */}
        {/* Result Row 1 Start */}
        <Row
          allProps={{
            rowIndex: 0,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[0],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
          }}
        />
        {/* Result Row 1 End */}

        {/* Result Row 2 Start */}
        <Row
          allProps={{
            rowIndex: 1,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[1],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
          }}
        />
        {/* Result Row 2 End */}

        {/* Result Row 3 Start */}
        <Row
          allProps={{
            yellowBorder: true,
            rowIndex: 2,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[2],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
          }}
        />
        {/* Result Row 3 End */}

        {/* Result Row 4 Start */}
        <Row
          allProps={{
            rowIndex: 3,
            correctResultRow: props.correctResultRow,
            messagePractice: props.messagePractice,
            correctResultCell: props.correctResultCell,
            result: props.result[3],
            started: props.inputDisable,
            rc: props.resultClick,
            prf: props.prf,
          }}
        />
        {/* Result Row 4 End */}
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    t: state.pMode.tr,
    b: state.pMode.br,
    c: state.pMode.c,
    p: state.pMode.p,
    carry: state.pMode.carry,
    result: state.pMode.result,
    showResult: state.pMode.selectResult,
    correctCarryCell: state.pMode.correctCarryCell,
    correctResultCell: state.pMode.correctResultCell,
    correctResultRow: state.pMode.correctResultRow,
    btnHide: state.pMode.btnHide,
    inputDisable: state.number.inputDisable,
    carryClick: state.pMode.carryClick,
    resultClick: state.pMode.resultClick,
    addZero: state.pMode.addZero,
    finalResultValues: state.pMode.finalResultValues,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    messagePractice: (m) => dispatch(reduxFunctions.messagePractice(m)),
    pcf: (v) => dispatch(reduxFunctions.pcf(v)),
    prf: (v) => dispatch(reduxFunctions.prf(v)),
  };
};

export default connect(mapStatetoProps, mapDispatchtoProps)(PracticeBoard);
