import React from "react";
import swal from "sweetalert";
// import React, { useState } from "react";
import classNames from "../../containers/PracticeBoard/PracticeBoard.module.css";

const Row = ({ allProps }) => {
  const digit = allProps.result ? allProps.result : {};
  // const rowIndex = allProps.rowIndex;
  // const [nextResult, setNextCheck] = useState();
  const resultCellFocus = (e) => {
    const resultCellsContainers = [
      ...document.querySelectorAll(`.${classNames.resultRow}`),
    ];
    const resultCells = document.querySelectorAll(`.${classNames.resultInput}`);
    const indexOfResultRow = resultCellsContainers.indexOf(e.target.parentNode);
    const indexOfResult = [
      ...resultCellsContainers[allProps.correctResultRow].childNodes,
    ].indexOf(e.target);
    if (allProps.started) {
      if (indexOfResultRow !== -1) {
        if (allProps.rc) {
          resultCells.forEach((c) => (c.style.backgroundColor = "transparent"));
          if (indexOfResult === allProps.correctResultCell) {
            // setNextCheck(indexOfResult);
            e.target.style.backgroundColor = "#ffffc0";
            allProps.messagePractice(
              "You got the result cell! Now enter the value of it."
            );
            allProps.prf(true);
          } else {
            e.target.style.backgroundColor = "#ff867c";
            allProps.messagePractice("Sorry try again");
            allProps.prf(false);
          }
        } else {
          swal(
            "Wrong Action!",
            "You have already chosen the right result cell.",
            "error"
          );
        }
      }
    } else {
      swal(
        "Wrong Action!",
        "Please end digits and start the mutliplication first.",
        "error"
      );
    }

    // resultCells[nextResult].style.backgroundColor = "#ffffc0";
  };

  let classes;
  if (allProps.yellowBorder) {
    classes = `${classNames.resultRow} ${classNames.yellowBorder}`;
  } else {
    classes = classNames.resultRow;
  }
  return (
    <div className={classes} onClick={(e) => resultCellFocus(e)}>
      <div className={classNames.resultInput} id="resultInput">
        <h1>{digit[0]}</h1>
      </div>

      <div className={classNames.resultInput} id="resultInput">
        <h1>{digit[1]}</h1>
      </div>

      <div className={classNames.resultInput} id="resultInput">
        <h1>{digit[2]}</h1>
      </div>

      <div className={classNames.resultInput} id="resultInput">
        <h1>{digit[3]}</h1>
      </div>

      <div className={classNames.resultInput} id="resultInput">
        <h1>{digit[4]}</h1>
      </div>

      <div className={classNames.resultInput} id="resultInput">
        <h1>{digit[5]}</h1>
      </div>
    </div>
  );
};

export default Row;
